.app-wrapper {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-areas:"h h" "n c ";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;

}

.app-wrapper-content {
    grid-area: c;
    background: linear-gradient(45deg, #E8CBC0,#636FA4);

}

